.jumbotron {
  background-color: rgba(255, 255, 255, 0);
  padding: 1rem 1rem;
}

.book-list-item {
  background-color: rgba(151, 151, 151, 0.158);
  padding: 2rem;
  margin: 1rem 0;
}

.btn-success {
  width: 100%;
}

.header {
  background-image: url("https://images.unsplash.com/photo-1507842217343-583bb7270b66?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2653&q=80");
  background-size: cover;
  height: 40vh;
}

.header h1 {
  font-size: 5rem;
  color: #b5edba;
}

/* TEXT */
body {
  background-color: #424241;
}

h3,
h4 {
  color: #b5edba;
}

p,
a {
  color: #e6f6e7;
}

a {
  text-decoration: none;
  -webkit-transition: 0.8s all !important;
  transition: 0.8s all !important;
}

/* NAV */
.bg-light {
  background-color: #b5edba !important;
}

/* HOVER */
a .btn:hover,
.btn-primary:hover {
  text-decoration: none;
  background-color: #b5edba;
  color: #424241;
}

.btn-success:hover {
  background-color: #85ac89;
  color: #424241;
}

/* BUTTONS  */
.btn-primary {
  background-color: #f06f32;
  border: none;
  margin-right: 2rem;
  width: 8rem;
  -webkit-transition: 0.8s all !important;
  transition: 0.8s all !important;
}

.btn-success {
  background-color: #b5edba;
  color: #424241;
  -webkit-transition: 0.8s all;
  transition: 0.8s all;
}
/* SEARCH */
.search-bar {
  padding-top: 2rem;
  width: 90%;
  margin: 0 auto;
}

/* RESULTS */
.description {
  padding-left: 1rem;
}

